import { ALL_TEAMS } from './AllTeams'

const makeTeam = (id) => ({
	id,
	name: ALL_TEAMS[id].name,
	slug: ALL_TEAMS[id].slug,
	points: 0,
	playedMatches: 0,
	wonMatches: 0,
	drawnMatches: 0,
	lostMatches: 0,
	goalsFor: 0,
	goalsAgainst: 0,
	goalsDifference: 0,
})

const makeGroup = (ids) =>
	ids.reduce((acc, id) => {
		acc[id] = makeTeam(id)
		return acc
	}, {})

// A-L segn TU EXCEL
