import PropTypes from 'prop-types'
import { Form } from 'react-bootstrap'

//  TEMPORAL: banderas desde /src/assets/flags/<slug>.png (fallback a default.png)
import defaultFlag from '../../assets/flags/default.png'
const flagsContext = require.context('../../assets/flags', false, /\.png$/)

const getFlagSrc = (slug) => {
	if (!slug) return defaultFlag

	const safe = String(slug).trim().toLowerCase()
	const file = `./${safe}.png`

	try {
		return flagsContext(file)
	} catch {
		return defaultFlag
	}
}

const GroupStageTeam = (props) => {
	const {
		direction,
		errors,
		goals,
		finished,
		marginTeamName,
		bgColor,
		matchNumber,
		paddingContainer,
		register,
		slug,
		team,
		teamId,
		teamName,
		completed,
	} = props

	const allowedKeys = [
		'ArrowLeft',
		'ArrowUp',
		'ArrowRight',
		'ArrowDown',
		'Backspace',
		'Delete',
		'Tab',
		'Ctrl',
		'F5',
		'F12',
		'Home',
		'End',
	]

	const name = `match${matchNumber}${team}Goals`
	const minValueAllowed = 0
	const maxValueAllowed = 99

	const flagSrc = getFlagSrc(slug)

	//  FIX anti-crash:
	// - Si el usuario no escribe nada, RHF puede darte undefined/NaN.
	// - Evitamos reventar el input (y warnings) forzando defaultValue a '' cuando no es nmero.
	const safeGoals =
		typeof goals === 'number' && Number.isFinite(goals) ? goals : ''

	return (
		<section className="col-6 px-0">
			<section
				className={`d-flex align-items-center justify-content-between shadow-sm ${direction} ${paddingContainer} match-rounded ${bgColor}`}>
				<article className={`d-flex align-items-center ${direction}`}>
					<img
						src={flagSrc}
						alt={teamName}
						onError={(e) => {
							e.currentTarget.onerror = null
							e.currentTarget.src = defaultFlag
						}}
						loading="lazy"
						width={37}
					/>
					<span className={`${marginTeamName} groupstage-team-name`}>
						{teamName}
					</span>
				</article>

				<article className="d-flex">
					<Form.Control
						type="hidden"
						{...register(`match${matchNumber}${team}`)}
						value={teamId}
					/>

					<Form.Control
						bsPrefix="-"
						className="form-control text-center input-score"
						name={name}
						type="number"
						placeholder="-"
						readOnly={completed}
						onKeyDown={(e) => {
							if (allowedKeys.includes(e.key)) return

							if (
								!/^[0-9]*$/.test(e.key) ||
								e.currentTarget.value.length + 1 > 2
							) {
								e.preventDefault()
							}
						}}
						min={minValueAllowed}
						max={maxValueAllowed}
						defaultValue={finished ? safeGoals : ''}
						{...register(name, {
							//  FIX anti-crash + validacin real:
							// valueAsNumber convierte '' -> NaN. Lo manejamos con validate.
							valueAsNumber: true,

							// no obligamos aqu; si quieres que "Calcular" marque errores,
							// RHF lo har con validate (y tu UI muestra errors).
							validate: (value) => {
								// RHF: vaco => NaN (por valueAsNumber)
								if (
									value === undefined ||
									Number.isNaN(value)
								) {
									return 'Debes ingresar los goles de este equipo'
								}
								if (!Number.isInteger(value)) {
									return 'El marcador debe ser numrico'
								}
								if (value < 0) {
									return 'El marcador debe ser mayor o igual a 0'
								}
								if (value > 99) {
									return 'El marcador debe ser hasta de dos cifras'
								}
								return true
							},
						})}
					/>
				</article>
			</section>

			<article className="d-flex my-1">
				<span className="match-data text-danger">
					{errors?.[name]?.message}
				</span>
			</article>
		</section>
	)
}

export default GroupStageTeam

GroupStageTeam.propTypes = {
	direction: PropTypes.string.isRequired,
	errors: PropTypes.object.isRequired,
	goals: PropTypes.number, //  puede venir undefined al inicio
	group: PropTypes.string.isRequired,
	finished: PropTypes.bool.isRequired,
	marginTeamName: PropTypes.string.isRequired,
	matchNumber: PropTypes.number.isRequired,
	bgColor: PropTypes.string.isRequired,
	paddingContainer: PropTypes.string.isRequired,
	slug: PropTypes.string.isRequired,
	team: PropTypes.string.isRequired,
	teamId: PropTypes.string.isRequired,
	teamName: PropTypes.string.isRequired,
	urlAssets: PropTypes.string, //  temporalmente no se usa, no rompas llamadas
	register: PropTypes.func.isRequired,
	completed: PropTypes.bool,
}
